@import "../LoginPage/LoginPage.scss";

.section-1 {
    background-color: #27ae60;
    color: White;
    width: 100vw;
    display: grid;
    align-content: center;
    justify-items: center;
    @include bp("phone") {
        height: 20vh;
    }
    @include bp("tablet") {
        height: 100vh;
    }
    .logo-container {
        display: flex;
        align-items: center;

        img {
            @include bp("phone") {
                height: 12vw;
            }
            @include bp("tablet") {
                height: 8vw;
            }
        }
        p {
            @include bp("phone") {
                font-size: 8vw;
            }
            @include bp("tablet") {
                font-size: 4vw;
            }
        }
    }

    h3 {
        margin: 0;

        @include bp("phone") {
            font-size: 4.6vw;
        }
        @include bp("tablet") {
            font-size: 2.3vw;
        }
    }
}
